const app = getApp()
export default {
	data() {
		return {
			indicatorDots: true,
			autoplay: true,
			interval: 2000,
			duration: 500,
			Inv: 0,
			boardId: '',
			pageNode: [],
			imgUrl: '',
			page_size: 10,
			page:1
		};
	},
	onLaunch() {

	},
	onShow() {
		this.imgUrl = getApp().globalData.imgUrl
		console.log(getApp().globalData.token)
		this.getList()
		// this.getToken()
	},
	onHide() {
		
	},
	methods: {
		// 导航详情
		goNavs(e) {
			// console.log(e.currentTarget.dataset.id)
			let link = e.currentTarget.dataset.link
			let bind_board = e.currentTarget.dataset.bind_board
			let id = e.currentTarget.dataset.id
			let name = e.currentTarget.dataset.name
			console.log(bind_board)
			if (bind_board == '0') {
				uni.navigateTo({
					url: `/pages/productSupermarket`
				})
			} else if(bind_board == '1') {
				uni.navigateTo({
					url: `/pages/indexAccurate?id=${id}&name=${name}`
				})
			}
			// //开通会员
			// if(id == 10){
			// 	uni.navigateTo({
			// 		url:`/pages/joinMember`
			// 	})
			// }

		},
		//
		selListType(e) {
			this.Inv = e.currentTarget.dataset.index
			this.boardId = e.currentTarget.dataset.block_id
		},
		// changeTab(Inv){
		// 	that.navIdx = Inv;
		// 	console.log(that.navIdx)
		// },
		// 轮播跳转
		goBanner(e) {
			console.log(e)
		},
		// 文章详情
		goDetail(e) {
			console.log(e)
			uni.navigateTo({
				url: `/pages/articleDetail`
			})
		},
		//获取数据
		getList() {
			uni.request({
				url: `${app.globalData.requestUrl}/index`,
				method: 'GET',
				success: res => {
					res = app.null2str(res)
					if (res.data.status_code == 200) {
						let pageNode = res.data.data
						this.pageNode = pageNode
						if (pageNode.length > 0){
							this.boardId = pageNode.board_data[0].block_id
						}
					} else {
						uni.showToast({
							title: res.data.message
						});
					}

				}
			})
		},
		//加载更多
		onReachBottom() {
			console.log(this.boardId)
			this.page ++;
			// console.log(this.pageNode.board_data[Inv].block_id)
			uni.request({
				url: `${app.globalData.requestUrl}/index-board-posts`,
				method: 'GET',
				data: {
					board_id: this.boardId,
					page_size:this.page_size,
					page:this.page
				},
				success: res => {
					res = app.null2str(res)
					if (res.data.status_code == 200) {
						if(res.data.data == ''){
							uni.showToast({
								title:"暂无数据",
								icon:"none"
							})
						}
						this.pageNode = res.data.data
					} else {
						uni.showToast({
							title: res.data.message
						});
					}

				}
			})
		}
	}
};
